import Vue from 'vue'
import Vuex from 'vuex'
import axios from 'axios'
Vue.use(Vuex);
const state = {
  url:'http://yonghui.idata.mobi/api/v1',
  url1:'http://10.67.241.218/yhportal/api/business/editfavorite?',
  userInfo:null
};

const mutations = {
  getUserInfo(){
    let userInfo = window.location.href;
    let index = userInfo.indexOf('?');
    let param = userInfo.substring(index+1);
    let params = {};
    try {
      params[param.split('=')[0].trim()] = param.split('=')[1].trim().replace('#/','');
      state.userInfo = params.userId
    }catch (e){
      state.userInfo = null
    }
  }
};
const getters = {
  count: function (state) {
    return state.count + 100
  }
};
const actions = {
  subAjax(context,options){
    axios({
      method:options.method,
      url: options.url,
      data: options.param
    }).then((res)=>{
      options.callback && options.callback(res);
    },err =>{
      console.log(err)
    })
  }
};
export default new Vuex.Store({
  state,
  mutations,
  actions
})



